@import './var.scss';
@import './theme';
@import './mixins';

.pair-detail-page,
.token-detail-page {
  .mobile {
    display: none;
  }
  .detail-trad-wrap {
    padding: 20px;
    margin-top: 30px;
    border-radius: 11px;
    background: $color-normal-card-bg;
    @include lightThemeOnly {
      box-shadow: 0px 0px 20px 0px rgba(16, 12, 29, 0.1);
    }
    .token-logo {
      border-radius: 50%;
      border: 1px solid #dbdbdb;
    }
    .single-pair {
      .pr-title {
        display: flex;
        align-items: center;
        img {
          display: block;
          width: 50px;
          height: 50px;
          background: #fff;
          &:nth-child(2) {
            margin-left: -10px;
          }
        }
        .stable-token-logo {
          width: auto;
          height: 50px;
          background: transparent;
        }
        .left10 {
          flex: 1;
          margin-left: 10px;
          font-size: 30px;
          font-family: PingFangSC-Semibold, PingFang SC;
          font-weight: 600;
          color: $color-text-default;
          line-height: 42px;
          .pair-detail-favourite-wrap {
            position: relative;
            top: 5px;
            display: inline-block;
            margin-left: 30px;
            width: 30px;
            height: 30px;
            vertical-align: top;
          }
        }
      }
      .pr-bottom {
        display: flex;
        align-items: center;
        margin-top: 30px;
        flex-wrap: wrap;

        .price-item {
          display: flex;
          align-items: center;
          margin-right: 50px;
          flex-shrink: 0;
          &:last-child {
            margin-right: 0;
          }
          img {
            display: block;
            margin-right: 10px;
            width: 20px;
            height: 20px;
            background: #fff;
          }
          .token-name {
            font-size: 20px;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: $color-text-default;
            line-height: 28px;
            &:hover {
              color: #c1a0ff;
            }
          }
        }
      }
      .view-button {
        padding: 11px 22px;
        font-size: 20px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: #ffffff;
        line-height: 28px;
        border-radius: 10px;
        margin-left: 20px;
        cursor: pointer;
      }
      .add-liq {
        background: #decdff;
        color: #5a14e1;
        &:hover {
          background: #bdc0f5;
        }
      }
      .trade {
        background: #6210ff;
        &:hover {
          background-color: #4711b4;
        }
      }
    }
  }
  .detail-card-wrap {
    margin: 30px auto;
    .detail-card-inner {
      display: flex;
      padding: 20px 0;
      background: $color-normal-card-bg;
      border-radius: 11px;
      @include lightThemeOnly {
        box-shadow: 0px 0px 20px 0px rgba(16, 12, 29, 0.1);
      }
      .detail-card-item {
        flex: 1;
        border-right: 1px solid $color-border-normal-card;
        &:last-child {
          border-right: 0;
        }
      }
    }

    .stats-card {
      padding: 0 30px;
      .title {
        font-size: 20px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: $color-text-default-5;
        line-height: 28px;
      }
      .stats-data {
        display: flex;
        margin-top: 20px;
        color: $color-text-default;
        align-items: flex-end;
      }
      .card-num {
        font-family: PingFangSC-Semibold, PingFang SC;
        font-size: 26px;
        line-height: 37px;
        margin-right: 10px;
        font-weight: 600;
      }
      .card-trend {
        position: relative;
        flex: 1;
        top: -5px;
        font-size: 15px;
        white-space: nowrap;
      }
    }
    .pool-token-card {
      margin-top: 9px;
      .flex-row {
        display: flex;
        align-items: center;
        margin-bottom: 8px;
        &:last-child {
          margin-bottom: 0;
        }
        img {
          display: block;
          margin-right: 10px;
          width: 20px;
          height: 20px;
          background: #fff;
          border: 1px solid #dbdbdb;
          border-radius: 50%;
        }
        span {
          font-size: 15px;
          font-family: PingFangSC-Semibold, PingFang SC;
          font-weight: 600;
          color: $color-text-default;
          line-height: 21px;
        }
      }
    }
  }
  .detail-chart-wrap {
    margin-top: 30px;
    margin-bottom: 60px;
  }
  .pair-info-wrap {
    margin-top: 30px;
    .table-wrap {
      border-radius: 20px;
      overflow: hidden;
    }
  }
}

.token-detail-page {
  .token-header-wrap {
    margin-top: 24px;
    .token-header-inner {
      display: flex;
      align-items: center;
      padding: 30px 0;
      color: $color-text-default;
      .token-header-main {
        display: flex;
        flex: 1;
        .token-logo {
          margin-right: 20px;
          width: 70px !important;
          height: 70px !important;
          border-radius: 100%;
          border: 1px solid #ddd;
          background: #fff;
        }
        .token-label {
          flex: 1;
          font-family: PingFangSC-Semibold, PingFang SC;

          .token-symbol {
            display: flex;
            align-items: center;
            font-size: 30px;
            font-weight: 600;
            line-height: 42px;
            .token-detail-favourite-wrap {
              margin-left: 30px;
              width: 30px;
              height: 30px;
            }
          }
          .sub {
            display: flex;
            align-items: center;
            margin-top: 7px;
          }
          .token-rate {
            margin-right: 10px;
            font-size: 20px;
            font-weight: 600;
            line-height: 28px;
          }
          .color {
            font-size: 15px;
            font-weight: 600;
            line-height: 21px;
          }
        }
      }
      .token-btn-list {
        display: flex;
        .token-btn {
          margin-right: 20px;
          padding: 11px 22px;
          line-height: 28px;
          font-size: 20px;
          border-radius: 10px;
          font-weight: 600;
          cursor: pointer;
          user-select: none;

          &:last-child {
            margin-right: 0;
          }
        }
        .add-liq {
          background: #decdff;
          color: #5a14e1;
          &:hover {
            background: #bdc0f5;
          }
        }
        .trade {
          background: rgba(98, 16, 255, 1);
          @include lightThemeOnly {
            color: #fff;
          }
          &:hover {
            background-color: #4711b4;
          }
        }
      }
    }
  }
  .detail-card-wrap {
    margin-top: 0;
    .stats-card {
      // padding-left: 70px;
      padding-right: 10px;
    }
  }
  .token-pair-wrap {
    margin: 60px 0;
  }
}

