.pair-fee-tag {
  vertical-align: bottom;
  padding: 2px 8px;
  font-size: 18px;
  background: $color-border-primary;
  border-radius: 10px;
  color: $color-text-default-7;
  margin-left: 10px;
}
.pair-fee-tag-mobile {
  vertical-align: bottom;
  padding: 1px 8px;
  font-size: 12px;
  background: $color-border-primary;
  border-radius: 10px;
  color: $color-text-default-7;
  margin-left: 10px;
}
